import React, { useEffect, useState } from 'react';
import { API_BASE_URL } from "../../config/api-config";
import { EventSourcePolyfill } from 'event-source-polyfill';
import Badge from '@mui/material/Badge';
import IconButton from '@mui/material/IconButton';
import NotificationsIcon from '@mui/icons-material/Notifications';

// 알림 버튼
const NotificationComponent = ({showMessage}) => {
    const [notifications, setNotifications] = useState([]);
    const [notificationOpen, setNotificationOpen] = useState(false);

    const accessToken = localStorage.getItem("ACCESS_TOKEN");

    useEffect(() => {
        if (!accessToken) {
            return;
        }

        var url = API_BASE_URL + "/v3/notification";

        // EventSource를 통해 SSE 연결
        const eventSource = new EventSourcePolyfill(url, {
            headers: {
                Authorization: "Bearer " + accessToken,
            },
            withCredentials: true,
            heartbeatTimeout: 60 * 60 * 1000,
        });


        // 이벤트 리스너 등록
        eventSource.addEventListener("notification", (event) => {
            try {
                const parsedData = JSON.parse(event.data);
                console.log(parsedData);
                setNotifications(prevNotifications => [parsedData, ...prevNotifications]);
            } catch (error) {
                console.error("Error parsing data:", error);
            }
        });

        eventSource.addEventListener("dumy", (event) => {
            try {
                console.log(event.data);
            } catch (error) {
                console.error("Error parsing data:", error);
            }
        });

        // 컴포넌트가 언마운트될 때 EventSource 닫기
        return () => {
            eventSource.close();
        };
    }, []);

    const handleNotificationClick = (relatedUrl) => {
        window.location.href = relatedUrl;
    }

    const handlerDropdownMenu = () => {
        setNotificationOpen(!notificationOpen);
    }

    return (
        <div className="notification-container">
            <IconButton
                size="large"
                aria-label="show new notifications"
                color="inherit"
                onClick={() => handlerDropdownMenu()}
            >
                <Badge badgeContent={notifications.length} color="error">
                    <NotificationsIcon />
                </Badge>
            </IconButton>
            <div className="notification-list">
                {notificationOpen && (
                    <div className="notifications">
                        {notifications.map(notification => (
                            <li key={notification.id} onClick={() => handleNotificationClick(notification.relatedUrl)}>
                                {notification.content}
                            </li>
                        ))}
                    </div>
                )}
            </div>
        </div>
    );
};

export default NotificationComponent;
